import React from 'react';


const Category = ({ title, isActive, handleClick, cssClass }) => (
  <li className={isActive ? `${cssClass} active` : cssClass} onClick={() => handleClick(title)}>
    <a href="#">{title}</a>
  </li>
);

export default Category;



