import React from 'react';


const FilterGroup = ({ category, options, handleFilterSelect }) => (
  <div className="filter-group">
    <div>{category}</div>

    {options.map(option =>
      <div key={option}>
        <label>
          <input type="checkbox" onClick={() => handleFilterSelect(category, option)} />
          {option}
        </label>
      </div>
    )}

  </div>
);

const FilterContainer = ({ filters, handleFilterSelect }) => (
  <div className="filter-container">
    <h4>FILTERS</h4>

    {filters.map(filter =>
      <FilterGroup
        key={filter.filterCategory}
        handleFilterSelect={handleFilterSelect}
        category={filter.filterCategory}
        options={filter.filterOptions}
      />
    )}

  </div>
);

export default FilterContainer;



