export const buildFilterByFormula = (filters) => {
  const filterQuery = filters.reduce((acc, filter) => {
    return [...acc, `Find(%27${filter.value}%27, ${filter.key})`]
  }, []);

  return `?filterByFormula=AND(${filterQuery.join(', ')})`;
};



