import React from 'react';

import Category from 'Category';


const CategoryContainer = ({ categories, handleCategoryClick, handleSubcategoryClick, selectedCategory, selectedSubcategory }) => (
  <div>
    <div className="categories">
      <ul className="nav">

        {categories.map(category =>
          <Category
            key={category.title}
            cssClass={'nav-item'}
            title={category.title}
            handleClick={() => { handleCategoryClick(category.title); handleSubcategoryClick(category.subcategories[0].title); }}
            isActive={category.title === selectedCategory}
          />
        )}

      </ul>
    </div>
    <div className="subcategories">
      <ul className="nav">
        {categories.find(category => category.title === selectedCategory)
          .subcategories.map(subcategory =>
            <Category
              key={subcategory.title}
              cssClass={'subcategory'}
              title={subcategory.title}
              handleClick={handleSubcategoryClick}
              isActive={subcategory.title === selectedSubcategory}
            />
          )
        }
      </ul>
    </div>
  </div>
);

export default CategoryContainer;



