import React from 'react';

//import logo from './logo.png'


const RecordsContainer = ({ records }) => (
  <div className="images">

    {records.map(record =>
      <div key={record.id}>
      <img src={record.thumbUrl} />
        <div>{record.title}</div>
      </div>
    )}

  </div>
);

export default RecordsContainer;



